<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10 zd-cm-res-fl"
              custom-icon="zd-common-icon-add" @click="addApply">
        申领单
      </Button>
      <div class="zd-common-fr zd-cm-res-fr">
        <DatePicker :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-common-input-h38  zd-common-fl zd-common-mr10 zd-cm-w220"
                    @on-change="changeDate"></DatePicker>
        <Select v-model="department" placeholder="全部申领部门"
                class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10" clearable>
          <Option v-for="item in departArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Select v-model="type" placeholder="全部领用类型"
                class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10" clearable>
          <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
        </Select>
        <Select v-model="status" placeholder="全部审核状态"
                class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10" clearable>
          <Option v-for="item in statusArr" :value="item" :key="item">{{ item }}</Option>
        </Select>
        <Input v-model="orderNO" :maxlength="50" placeholder="申领单号"
               class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--添加申领单-->
    <Modal v-model="addModal" :mask-closable="false" :width="1200" >
      <h3 slot="header" class="zd-common-modal-header">
        <span>{{ modalTitle }}</span>
      </h3>
      <div class="zd-common-h400">
        <vue-scroll>
          <div class="zd-common-pr20">
            <h3 class="zd-common-tc zd-common-mb20 zd-common-fs16">{{ schoolName }}物品申领单</h3>
            <Form class="zd-cm-applyForm" ref="addForm" :model="addForm" :rules="ruleValidate" :label-width="85" inline>
              <!--<FormItem label="申领单号：" prop="applyNum">
                <Input v-model.trim="addForm.applyNum" placeholder="申领单号" class="zd-common-w150" :maxlength="30"></Input>
              </FormItem>-->
              <FormItem label="申领部门：" prop="applyDepart">
                <Select v-model="addForm.applyDepart" placeholder="选择申领部门" class="zd-common-w150" @on-change="changeDepart">
                  <Option v-for="item in departArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
                </Select>
              </FormItem>
              <FormItem v-if="isShowCheckUser" label="部门审核人：" prop="departCheckUser" :label-width="100">
                <Select v-model="addForm.departCheckUser" placeholder="选择审核人" class="zd-common-w150">
                  <Option v-for="item in departCheckUserArr" :value="item.id" :key="item.id">{{ item.name }}</Option>
                </Select>
              </FormItem>
              <FormItem label="申领类型：" prop="applyType">
                <Select v-model="addForm.applyType" placeholder="选择申领类型" class="zd-common-w150">
                  <Option v-for="item in typeArr" :value="item.code" :key="item.code">{{ item.name }}</Option>
                </Select>
              </FormItem>
            </Form>
            <div class="zd-common-black-b zd-common-pt5 zd-common-pb5 zd-common-pl20">
              <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="addGoodsHandle">
                添加物品
              </Button>
              <Button type="default" class="zd-common-mr10 zd-common-h32 zd-common-lh8" @click="delRowHandle">
                删除
              </Button>
            </div>
            <Form ref="formGoodsData" :model="formGoodsData" class="zd-common-mt10">
              <Table class="zd-common-overflow-v" border :columns="goodsCols" :data="formGoodsData.attrData" :row-class-name="rowClassName" @on-row-click="clickRowHandle">
                <template slot-scope="{ row,index }" slot="name">
                  <Form-item class="zd-common-mt24">
                    {{ row.name }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="code">
                  <Form-item class="zd-common-mt24">
                    {{ row.code }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="classify">
                  <Form-item class="zd-common-mt24">
                    {{ row.classify }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="brand">
                  <Form-item class="zd-common-mt24">
                    {{ row.brand }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="model">
                  <Form-item class="zd-common-mt24">
                    {{ row.model }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="unit">
                  <Form-item class="zd-common-mt24">
                    {{ row.unit }}
                  </Form-item>
                </template>
                <template slot-scope="{ row,index }" slot="number">
                  <Form-item class="zd-common-mt24" :prop="'attrData.' + index + '.number'"
                             :rules="{required: true, message:'不能为空', trigger:'blur', type:'number'}">
                    <InputNumber :max="100" :min="1" :precision='0' placeholder="数量" v-model="formGoodsData.attrData[index].number"/>
                  </Form-item>
                </template>

              </Table>
            </Form>
            <div class="zd-common-mt10 zd-common-clearFix">
              <span class="zd-common-fl">申领人：{{ userName }}</span>
              <span class="zd-common-fl zd-common-ml20">申领日期：{{ date }}</span>
            </div>
            <Form ref="addFormReason" :model="addFormReason" class="zd-common-mt20">
              <FormItem label="申领原因：" class="zd-common-mb0">
                <Input style="width: 798px;" class="zd-common-fl" v-model.trim="addFormReason.applyReason" placeholder="申领原因" :maxlength="100" type="textarea" :autosize="{minRows: 2,maxRows: 5}"></Input>
              </FormItem>
            </Form>
          </div>
        </vue-scroll>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit"
                :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="addModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--添加物品-->
    <Modal v-model="addGoodsModal" :mask-closable="false" :width="1200" >
      <h3 slot="header" class="zd-common-modal-header">
        <span>物品档案</span>
      </h3>
      <add-goods ref="child"></add-goods>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="saveGoodsHandle">确定</Button>
        <Button @click="addGoodsModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--查看申领单-->
    <Modal v-model="viewModal" :mask-closable="false" :width="920" ref="print">
      <h3 slot="header" class="zd-common-modal-header">
        <span>查看申领单</span>
      </h3>
      <div class="zd-common-h400">
        <vue-scroll>
          <div class="zd-common-pr20">
            <div v-if="msgList.length>0" class="zd-common-h100 zd-common-border-radius6 zd-cm-process zd-common-mb20 no-print">
              <vue-scroll>
                <div class="zd-cm-step zd-common-clearFix">
                  <template v-for="(item,index) in msgList">
                    <div class="zd-cur-step zd-common-fl zd-common-ml20 zd-common-mt29">
                      <p>{{ item.time }}</p>
                      <p>{{ item.userName }}：{{ item.status }}</p>
                    </div>
                    <div v-if="index !== msgList.length - 1"
                         class="zd-cur-step zd-common-fl zd-common-ml20 zd-common-lh100">
                      <Icon type="ios-arrow-forward"/>
                    </div>
                  </template>
                </div>
              </vue-scroll>
            </div>
            <div class="zd-common-clearFix">
              <span class="zd-common-fl zd-common-mr14">申领单号：<span class="zd-common-font-weight">{{ allDetail.num }}</span></span>
              <span class="zd-common-fl zd-common-mr14">申领日期：<span class="zd-common-font-weight">{{ allDetail.date }}</span> </span>
              <span class="zd-common-fl zd-common-mr14">申领人：<span class="zd-common-font-weight">{{ allDetail.userName }}</span> </span>
              <span class="zd-common-fl zd-common-mr14">申领部门：<span class="zd-common-font-weight">{{ allDetail.departmentName }}</span> </span>
              <span v-if="allDetail.deptCheckName" class="zd-common-fl zd-common-mr14">部门审核人：<span class="zd-common-font-weight">{{
                      allDetail.deptCheckName }}</span></span>
              <span class="zd-common-fl zd-common-mr14">领用类型：<span class="zd-common-font-weight">{{ allDetail.kindName }}</span></span>
            </div>
            <Table class="zd-common-mt10" border :columns="viewCols" :data="viewData" show-summary></Table>
            <div class="zd-common-mt10 zd-common-clearFix">
                      <span class="zd-common-clearFix">
                        <span class="zd-common-fl">申领原因：</span>
                        <span class="zd-common-fl zd-cm-applyReason">{{ allDetail.reason }}</span>
                      </span>
            </div>
          </div>
        </vue-scroll>
      </div>
      <div slot="footer" class="zd-common-modal-footer no-print">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="printHandle">打印</Button>
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="reApplyHandle">再次申请
        </Button>
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--删除-->
    <Modal v-model="delModal" :mask-closable="false" :width="360">
      <h3 slot="header"></h3>
      <div class="zd-common-tc">
        <div class="zd-common-modal-waring zd-common-auto"></div>
        <p class="zd-common-primary-c zd-common-font-size-large zd-common-mt15">是否确认删除？</p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr24 zd-common-w100 zd-common-h32 zd-common-lh8"
                :loading="saveLoading" @click="confirmDelHandle">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在删除...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import { utils } from '@comm/script/utils/index'
  import AddGoods from '../../components/addGoods'
  export default {
    components:{
      AddGoods
    },
    data() {
      return {
        //添加申领单
        addModal:false,
        modalTitle:'添加申领单',
        addForm:{
          applyDepart:'',
          departCheckUser:'',
          applyType:''
        },
        addFormReason:{
          applyReason:''
        },
        ruleValidate: {
          applyDepart: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
          departCheckUser: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
          applyType: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
        },
        applyDepartArr:[],
        departCheckUserArr:[],
        isShowCheckUser:false,
        saveLoading:false,
        schoolName:'',
        userName:'',
        date:'',

        //添加的物品
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            slot: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            slot: 'code',
          },
          {
              title: '物品分类',
              align: 'center',
              slot: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            slot: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            slot: 'model',
          },
          {
            title: '单位',
            align: 'center',
            slot: 'unit',
          },
          {
            title: '数量',
            align: 'center',
            slot: 'number',
            renderHeader:(h,params)=>{
              return h('div',[
                h('span', {
                  class:'zd-cm-required'
                },'数量'),
              ])
            }
          }
        ],
        formGoodsData:{
          attrData:[]
        },

        //添加物品
        addGoodsModal:false,
        delId:'',
        delIndex:'',

        //查询
        startDate:'',
        endDate:'',
        departArr:[],
        department:'',
        typeArr:[],
        type:'',
        statusArr:[],
        status:'',
        orderNO:'',
        dateOptions: this.dateRange,

        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '申领单号',
            align: 'center',
            key: 'applyNum',
          },
          {
            title: '申领部门',
            align: 'center',
            key: 'applyDepart',
          },
          {
            title: '申领日期',
            align: 'center',
            key: 'applyDate',
          },
          {
            title: '申领类型',
            align: 'center',
            key: 'applyType',
          },
          {
            title: '物品数量',
            align: 'center',
            key: 'goodsNum',
          },
          {
            title: '当前状态',
            align: 'center',
            key: 'status',
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:240,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                    marginRight:'10px'
                  },
                  on: {
                    click: () => {
                      this.showViewModal(params.row.id);
                    }
                  }
                }, '查看'),
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                    marginRight:'10px'
                  },
                  on: {
                    click: () => {
                      this.showEditModal(params.row.id);
                    }
                  }
                }, '修改'),
                h('Button', {
                  props:{
                    type:'error',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px'
                  },
                  on: {
                    click: () => {
                      this.showDelModal(params.row.id);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        selectLoading:false,

        //申领单查看
        viewModal:false,
        viewCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:70
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '数量',
            align: 'center',
            key: 'number'
          },
        ],
        viewData:[],
        allDetail:[],
        msgList:[],

        //再次申请
        reApplyId:'',

        //修改
        editId:'',
        editOrderNO:'',

        //删除
        delModal:false,
      }
    },
    computed:{

    },
    created(){
      this.getDepartment().then((response)=>{
        this.departArr = response.data;
      });
      this.getApplyKindList();
      this.getApplyStatus();
      this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      this.$store.dispatch('getBaseInfo').then((res) => {
        this.schoolName = res.schoolName;
        this.userName = res.userName;
      });
    },
    methods:{
      //打印
      printHandle(){
        // window.print();
        this.$print(this.$refs.print)
      },
      //添加
      addApply(){
        this.editId = '';
        this.modalTitle = '添加申领单';
        this.addFormReason.applyReason = "";
        this.addModal = true;
        this.handleReset('addForm');
        this.handleReset('formGoodsData');
        this.handleReset('addFormReason');
        this.formGoodsData.attrData = [];
        this.$refs.child.selectedArr = [];
        this.$refs.child.selectedArrId = [];
        this.date = new Date().getFullYear() +"-"+ this.fillZero(new Date().getMonth()+1) +"-"+ this.fillZero(new Date().getDate());
      },

      //保存验证
      handleSubmit(){
        let flag = false,flag2 = false;
        this.$refs['addForm'].validate((valid) => {
          if (valid) {
            flag = true;
          }
        });
        if(this.formGoodsData.attrData.length<=0){
          this.postWarningTip('请添加申领物品');
        }else{
          this.$refs['formGoodsData'].validate((valid) => {
            if (valid) {
              flag2 = true;
            }
          });
          if(flag && flag2){
            this.saveLoading = true;
            this.addModal = false;
            this.saveOrUpdateApplyReceiveRecord(this.addForm.applyDepart,this.addForm.departCheckUser,this.addForm.applyType,this.formGoodsData.attrData,this.addFormReason.applyReason,this.editOrderNO,this.editId);
          }
        }
      },
      saveOrUpdateApplyReceiveRecord(department,deptCheckUser,kind,arr,reason,code,id){
        let params = {};
        params['applyReceiveRecord.department.id'] = department;
        if(this.isShowCheckUser){
          params['applyReceiveRecord.deptCheckUser.id'] = deptCheckUser;
        }
        params['applyReceiveRecord.kind'] = kind;
        params['applyReceiveRecord.reason'] = reason;
        params['applyReceiveRecord.code'] = code;
        params['applyReceiveRecord.id'] = id;
        arr.map((item,index)=>{
          params['outGoodsVOs['+index+'].count'] = item.number;
          params['outGoodsVOs['+index+'].recordGoodsId'] = item.id;
        });
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveOrUpdateApplyReceiveRecord.action',
          data:params,
        })
          .then((response) => {
            this.saveLoading=false;
            this.postSuccessTip('申领成功');
            this.pageNum = 1;
            this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
          })
          .catch((error) => {
            this.saveLoading=false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //重置
      handleReset (name) {
        this.$refs[name].resetFields();
      },

      //添加物品
      addGoodsHandle(){
        this.addGoodsModal=true;
        let addGood = this.formGoodsData.attrData;
        let addGoodId = [];
        addGood.map((item)=>{
          addGoodId.push(item.id);
        });
        this.$refs.child.getKind('2');
        this.$refs.child.selectedArr = addGood.slice();
        this.$refs.child.selectedArrId = addGoodId;
      },
      saveGoodsHandle(){
        this.addGoodsModal = false;
        this.formGoodsData.attrData = this.$refs.child.selectedArr.slice();
      },
      //删除添加的物品
      clickRowHandle(row,index){
        if(this.delId === row.id){
          this.delId = "";
          this.delIndex = "";
        }else{
          this.delId = row.id;
          this.delIndex = index;
        }
      },
      rowClassName(row, index) {
        if (this.delId === row.id) {
          return 'demo-table-info-row';
        }
        return '';
      },
      delRowHandle(){
        if(this.delIndex === ""){
          return this.postWarningTip('请选择删除的物品');
        }else{
          this.formGoodsData.attrData.splice(this.delIndex,1);
          if(this.$refs.child.selectedArrId.indexOf(this.delId)>-1){
            let index = this.$refs.child.selectedArrId.indexOf(this.delId);
            this.$refs.child.selectedArr.splice(index,1);
            this.$refs.child.selectedArrId.splice(index,1);
          }
        }
      },
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //获取类型
      getApplyKindList() {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getApplyKindList.action',
          data:{},
        })
          .then((response) => {
            this.typeArr = response.data;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //根据部门获取审核人
      getCheckUser(departmentId){
        this.getCheckUserByDepartmentId(departmentId).then((response)=>{
          let aData = response.data;
          this.isShowCheckUser = aData.departmentCheck;
          this.departCheckUserArr = aData.users;
        })
      },
      changeDepart(id){
        this.getCheckUser(id);
      },
      //获取状态
      getApplyStatus(){
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getApplyStatus.action',
          data:{},
        })
          .then((response) => {
            let aData = response.data;
            this.statusArr = response.data;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //列表
      getApplyReceiveRecordPage(pageNum,pageSize,startDate,endDate,departmentId,kind,status,num) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getApplyReceiveRecordPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            departmentId:departmentId,
            kind:kind,
            status:status,
            num:num,
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  applyNum:item.num,
                  applyDepart:item.department,
                  applyDate:item.date,
                  applyType:item.kindName,
                  goodsNum:item.goodsNum,
                  status:item.status,
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
      //申领单查看
      showViewModal(id){
        this.editId = '';
        this.editOrderNO = '';
        this.reApplyId = id;
        this.viewModal = true;
        this.getApplyReceiveRecordInfo(id);
      },
      getApplyReceiveRecordInfo(id) {
        this.viewData = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getApplyReceiveRecordInfo.action',
          data:{
            id:id
          },
        })
        .then((response) => {
          let aData = response.data[0];
          //详情
          this.allDetail = aData;
          this.msgList = aData.msgList;
          aData.goodsList.map((item)=>{
            this.viewData.push(
              {
                name:item.name,
                code:item.code,
                classify:item.goodsKind,
                brand:item.brand,
                model:item.size,
                unit:item.unit,
                number:item.count,
                id:item.id
              }
            )
          });
          //修改
          this.addForm.applyDepart = aData.departmentId;
          this.addForm.departCheckUser = aData.deptCheckId ? aData.deptCheckId : '';
          this.addForm.applyType = aData.kind;
          this.addFormReason.applyReason = aData.reason;
          this.formGoodsData.attrData = this.viewData.slice();
          this.editOrderNO = aData.num;
          this.userName = aData.userName;
          this.date = aData.date;
        })
        .catch((error) => {
          this.postErrorTip('请求失败');
          console.log(error);
        });
      },
      reApplyHandle(){
        this.viewModal = false;
        this.modalTitle = '添加申领单';
        this.addModal = true;
        this.getApplyReceiveRecordInfo(this.reApplyId);
      },
      //修改
      showEditModal(id){
        this.modalTitle = '修改申领单';
        this.editId = id;
        this.addModal = true;
        this.getApplyReceiveRecordInfo(id);
      },
      //删除
      showDelModal(id){
        this.editId = id;
        this.delModal = true;
      },
      deleteApplyReceiveRecord(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!deleteApplyReceiveRecord.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData !== ''){
              this.postWarningTip(aData);
            }
            this.saveLoading = false;
            this.getApplyReceiveRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      confirmDelHandle(){
        this.delModal = false;
        this.saveLoading = false;
        this.deleteApplyReceiveRecord(this.editId);
      },
    },
    mounted(){
      this.calcWidth();
    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
  /*适配*/
  @media screen and (max-width: 1305px){
    .zd-cm-res-fr{
      float: left !important;
    }
    .zd-cm-res-fr > *,.zd-cm-res-fl{
      margin-bottom:10px;
    }
  }
</style>



